<template>
  <div>
    <van-tabs
      @change="checkValue"
      title-active-color="#ff6623"
      color="#ff6623"
    >
      <van-tab
        title="未收款账单"
        name="0"
      > </van-tab>
      <van-tab
        title="已收款账单"
        name="1"
      ></van-tab>
    </van-tabs>
    <div
      v-if='listArr.length > 0 && actived == 0'
      :key="contentKey"
      class="agreementScroll"
      :style="{ height: actived == 0 ? 'calc(100vh - 150px)' : 'calc(100vh - 110px)' }"
    >
      <div
        v-for="(item, i) in listArr"
        :key="i"
      >
        <div
          v-if="actived == 0"
          class="noPaid-time"
          @click="openMonth(item)"
        >
          <div>
            <span class="time-title">{{ item.date }}月账单</span>
            <!-- reducePrice -->
            <span class="time-price"> 总金额 {{ item.priceTotle | PriceF }}元 </span>
          </div>
          <div>
            <van-icon :name="item.statu_month?'arrow-down':'arrow'" />
          </div>
        </div>
        <!-- 每天 -->
        <div v-if="item.statu_month == true">
          <ul>
            <li
              class="my-li"
              v-for="(item_o, i) in item.itemList"
              :key="i"
            >
              <div class="day-content content-top mr10">
                <div class="day-content w100">
                  <van-icon
                    @click="isChoosed(item_o)"
                    :name="item_o.isCheck == 'checked'?'passed':'circle'"
                    size="16px"
                    :style="{ color: !item_o.isCheck? '#9a9b9c' : '#ff7d41' }"
                  />
                  <div class="content-left">
                    <div class="my-color-000">
                      <div style="max-width:40%;font-weight:bold;">{{ item_o.roomSn || '' }}</div>
                      <div :class="item_o.title == '租金'?'db':'ff'">{{ item_o.title }}</div>
                    </div>
                    <div style="white-space:nowrap;">{{ item_o.startTime | dataFilter }}~{{ item_o.endTime | dataFilter }}</div>
                  </div>
                </div>
                <div
                  class="my-van-icon"
                  @click="contentKey++, item_o.isOpen = !item_o.isOpen"
                >
                  <span
                    class="my-color-000"
                    style="font-weight:bold;"
                  >{{ item_o.totalPrice | PriceF }}元</span>
                  <van-icon name="arrow" />
                </div>
              </div>
              <div v-if="item_o.isOpen">
                <div class="day-content m40">
                  <div>缴费周期</div>
                  <div>{{ item_o.startTime | dataFilter }}至{{ item_o.endTime | dataFilter }}</div>
                </div>
                <div
                  v-if="item_o.payTime"
                  class="day-content m40 content-top"
                >
                  <div>收款日期</div>
                  <div>{{ item_o.payTime | dataFilter }}</div>
                </div>
                <div class="day-content m40 content-top">
                  <div>备注</div>
                  <div>{{ paymentObj[item_o.payment] }}</div>
                </div>
                <div
                  class="p10"
                  @click="contentKey--, item_o.isOpen = false"
                >
                  <span>收起</span>
                  <van-icon name="arrow-up" />
                </div>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <!-- 已收款 -->
    <div
      v-if="actived == 1"
      class="payed_box"
      :key="contentKey"
    >
      <div class="payed_top">
        <div
          class="top_l"
          @click="show_date = true"
        >
          <div>{{ choosedTime }}</div>
          <van-icon name="arrow-down" />
        </div>
        <div style="font-size:0.7rem;">总金额 {{ payedTotelPrice }}元</div>
      </div>
      <div
        v-for="(item,i) in payedAll"
        :key="i"
        class="my-li"
      >
        <div class="day-content content-top mr10">
          <div class="day-content w100">
            <div class="content-left">
              <div class="my-color-000">
                <div style="max-width:40%;font-weight:bold;">{{ item.roomSn }}</div>
                <div
                  :class="item.title == '租金'?'db':'ff'"
                  class="my-li"
                >{{ item.title }}</div>
              </div>
              <div style="white-space:nowrap;color:#B5B5B5;">{{ item.startTime | dataFilter }}~{{ item.endTime | dataFilter }}</div>
            </div>
          </div>
          <div
            class="my-van-icon"
            @click="contentKey++, item.isOpen = !item.isOpen"
          >
            <span
              class="my-color-000"
              style="font-weight:bold;"
            >{{ item.totalPrice }}元</span>
            <van-icon :name="myLookMoreStyle" />
          </div>
        </div>
        <div v-if="item.isOpen">
          <div class="day-content m30">
            <div>缴费周期</div>
            <div>{{ item.startTime | dataFilter }}至{{ item.endTime | dataFilter }}</div>
          </div>
          <div
            v-if="item.payTime"
            class="day-content m30 content-top"
          >
            <div>收款日期</div>
            <div>{{ item.payTime | dataFilter }}</div>
          </div>
          <div class="day-content m30 content-top">
            <div>备注</div>
            <div>{{ paymentObj[item.payment] }}</div>
          </div>
          <div
            class="p10"
            @click="contentKey--,item.isOpen = false"
          >
            <span>收起</span>
            <van-icon name="arrow-up" />
          </div>
        </div>
      </div>
    </div>
    <div
      class="bottom-allPrice"
      v-if="actived == 0"
    >
      <div
        class="bottom-left"
        @click="isAllChecked"
      >
        <van-icon
          :name="myAllCheckedStyle"
          :style="{ color: myAllCheckedStyle == 'checked' ? '#ff7d41' : '#9a9b9c' }"
        />&ensp;全选
      </div>
      <div class="bottom-center">
        总计<label>
          &yen; <span>{{ payForm.totalPrice | PriceF }}</span></label>
      </div>
      <van-button
        class="bottom-right"
        @click="btnGo"
      >到账</van-button>
    </div>
    <!-- 到账 -->
    <van-popup
      v-model="showPayed"
      round
      bind:close="onClose"
    >
      <h3>到账</h3>
      <van-cell-group>
        <van-form @submit="confirmPayed">
          <van-field
            v-model="payForm.payTime"
            readonly
            label="到账日期"
            placeholder="点击选择到账日期"
            @click.native="timeShow = true"
            :rules="[{ required: true, message: '请选择到账日期' }]"
          />
          <van-field
            v-model="payForm.paymentValue"
            readonly
            label="支付方式"
            placeholder="请选择"
            @click="paymentShow = true"
            :rules="[{ required: true, message: '请选择支付方式' }]"
          />
          <!-- <van-field v-model="payForm.payeeAccountIdValue"
                     readonly
                     label="收款账户"
                     @click="payeeAccountIdValueShow = true" /> -->
          <van-field
            v-model="payForm.totalPrice"
            readonly
            label="到账金额"
          />
          <van-field
            v-model="payForm.username"
            label="确认人"
            readonly
          />
          <van-field
            v-model="payForm.remark"
            label="备注"
            placeholder="请输入备注"
          />
          <van-button class="payedColor">确定收款</van-button>
        </van-form>
      </van-cell-group>
    </van-popup>
    <!-- 点击选择日期---开始日期 -->
    <mt-popup
      v-model="show_date"
      position="bottom"
      style="width:100%;"
    >
      <van-datetime-picker
        v-model="c_date"
        type="year-month"
        title="选择开始日期"
        :min-date='chooseMinDate'
        :max-date='chooseMaxDate'
        @cancel='show_date = false'
        @confirm="onInputBeforeDate"
      />
    </mt-popup>
    <!-- 到账日期 -->
    <van-calendar
      style="width: 100%"
      v-model="timeShow"
      :min-date="minDate"
      :max-date="maxDate"
      @close="timeShow = false"
      @confirm="onConfirmTime"
    />
    <!-- 支付方式 -->
    <van-popup
      style="width: 100%"
      v-model="paymentShow"
      position="bottom"
    >
      <van-picker
        show-toolbar
        :columns="payments"
        @confirm="onConfirmPayment"
        @cancel="paymentShow = false"
      />
    </van-popup>
    <!-- 收款账户 -->
    <van-popup
      style="width: 100%"
      v-model="payeeAccountIdValueShow"
      position="bottom"
    >
      <van-picker
        show-toolbar
        :columns="users"
        @confirm="onConfirm"
        @cancel="payeeAccountIdValueShow = false"
      />
    </van-popup>
  </div>
</template>

<script>
import { GetSystemDictDataByType } from '@/api/api_query';
import { GetBusinessHotelPayeeAccountByhotel } from '@/api/api_business';
import { GetBusinessCooperRentalBillPayedBycooper, GetBusinessCooperRentalBillUnpayBycooper, PostBusinessCooperRentalBillAllPayOffline } from '@/api/api_two_landlord';
import { Toast } from 'mint-ui';
import dayjs from 'dayjs';
export default {
    name: 'index',
    data() {
        return {
            businessList: [],
            key: 0,
            listArr: [],
            item: '',
            statu: false,
            options: {},
            s: '',
            priceAll: {},
            refuseAll: {},
            selected: '1',
            contentKey: 0,
            day: 14,
            timecheckValue: null,
            currentMounth: null,
            outBill: null,
            reducePrice: null,
            payedTotelPrice: null,
            num: '1',
            showRoomPayed: false,
            showPayed: false,
            value: '0',
            actived: '0',
            topValue: ['未收款账单', '已收款账单'],
            payedAll: [],
            roomStyle: 'circle',
            myAllCheckedStyle: 'circle',
            myLookMoreStyle: 'arrow',
            chooseMinDate: new Date(dayjs().subtract(2, 'year')),
            chooseMaxDate: new Date(dayjs().subtract(1, 'day').format('YYYY-MM-DD')),
            c_date: null,
            roomPrice: 0,
            timeShow: false,
            payeeAccountIdValueShow: false,
            show_date: false,
            paymentShow: false,
            minDate: null,
            maxDate: null,
            choosedTime: null,
            paymentObj: null,
            users: [],
            payments: [],
            checkAllList: [],
            dataForm: {
                billMonth: '',
                cooperId: ''
            },
            payForm: {
                billSn: '',
                payTime: '',
                paymentValue: '',
                payment: '',
                totalPrice: 0,
                username: null,
                remark: ''
            },
            checkedCount: 0
        };
    },
    filters: {
        PriceF(val) {
            return Number(val).toFixed(2);
        }
    },
    watch: {
        checkAllList(neww) {
            if (neww.length == 0) {
                this.myAllCheckedStyle = 'circle';
            }
        },
        listArr(neww) {
            neww.map((item) => {
                let aaaaa;
                if (item.statu_month == true) {
                    aaaaa = item.itemList.reduce(function (total, item) {
                        return total + item.totalPrice;
                    }, 0);
                }
                item.priceTotle = aaaaa;
            });
        }
    },
    created() {
        this.$store.commit('setHeader', this.$route.query.cooperationName);
        this.choosedTime = dayjs().format('YYYY年MM月');
        this.minDate = new Date(dayjs().subtract(6, 'month'));
        this.maxDate = new Date(dayjs().add(6, 'month'));
        this.dataForm.cooperId = this.$route.query.cooperId;
        this.getPayment();
    },
    methods: {
        // 支付方式
        getPayment() {
            GetSystemDictDataByType({ dictType: 'business_payment' }).done((res) => {
                this.payForm.username = JSON.parse(localStorage.getItem('current_user')).realName || JSON.parse(localStorage.getItem('current_user')).nickName;
                this.paymentObj = {};
                res.data.map((item) => {
                    this.paymentObj[item.dictValue] = item.dictLabel;
                });
                this.paymentsAll = res.data.filter((item) => {
                    return item.dictValue >= 5;
                });
                this.payments = res.data
                    .filter((item) => {
                        return item.dictLabel && item.dictValue >= 5;
                    })
                    .map((item) => item.dictLabel);
                this.getUnpayInfo();
            });
        },
        getUnpayInfo() {
            delete this.dataForm.billMonth;
            GetBusinessCooperRentalBillUnpayBycooper(this.dataForm).done((res) => {
                this.listArr = res.data;
                if (this.listArr.length > 0) {
                    this.listArr.map((item) => {
                        item.isCheck = '';
                        item.isOpen = false;
                        item.title = item.billSn.charAt(0) == 'L' ? '生活' : '租金';
                        item.startM = dayjs(item.startTime).format('MM');
                    });
                    var mainList = [];
                    for (var i = 0; i < this.listArr.length; i++) {
                        var num = mainList.length;
                        var date = this.listArr[i].startM;
                        if (num > 0 && !!mainList[num - 1].date && mainList[num - 1].date == date) {
                            mainList[num - 1].itemList.push(this.listArr[i]);
                        } else {
                            var item = {
                                date: date,
                                statu_month: true,
                                priceTotle: null,
                                itemList: []
                            };
                            item.itemList.push(this.listArr[i]);
                            item.itemList;
                            mainList.push(item);
                        }
                    }
                    ++this.contentKey;
                    this.listArr = mainList.reverse();
                }
            });
        },
        // 已支付
        getPayInfo() {
            GetBusinessCooperRentalBillPayedBycooper(this.dataForm).done((res) => {
                this.payedAll = res.data;
                res.data.map((item) => {
                    item.isOpen = false;
                    item.title = item.billSn.charAt(0) == 'L' ? '生活' : '租金';
                });
                this.payedTotelPrice = res.data.reduce(function (total, item) {
                    return total + item.totalPrice;
                }, 0);
                ++this.contentKey;
            });
        },
        checkValue(e) {
            this.actived = e;
            if (e == 0) {
                delete this.dataForm.billMonth;
                this.checkAllList = [];
                this.myAllCheckedStyle = 'circle';
                this.getUnpayInfo();
            } else {
                this.dataForm.billMonth = this.choosedTime = dayjs().format('YYYY-MM');
                this.c_date = new Date(dayjs().format('YYYY-MM'));
                this.getPayInfo();
            }
        },
        openMonth(item) {
            item.statu_month = !item.statu_month;
        },
        isChoosed(item) {
            if (!item.isCheck) {
                item.isCheck = 'checked';
                this.checkAllList.push(item);
                this.payForm.totalPrice = this.checkAllList.reduce(function (total, item) {
                    return total + item.totalPrice;
                }, 0);
            } else {
                item.isCheck = '';
                this.checkAllList = this.checkAllList.filter((itm) => {
                    return itm.billSn != item.billSn;
                });
                this.payForm.totalPrice = this.payForm.totalPrice - item.totalPrice;
            }
            this.contentKey++;
        },
        // 全选
        isAllChecked() {
            this.checkAllList = [];
            if (this.myAllCheckedStyle == 'circle') {
                this.myAllCheckedStyle = 'checked';
                this.listArr.map((item) => {
                    if (item.statu_month == true) {
                        item.itemList.map((child) => {
                            this.checkAllList.push(child);
                            child.isCheck = 'checked';
                        });
                    }
                });
                this.payForm.totalPrice = this.checkAllList.reduce(function (total, child) {
                    return total + child.totalPrice;
                }, 0);
            } else {
                this.myAllCheckedStyle = 'circle';
                this.listArr.map((item) => {
                    item.itemList.map((child) => {
                        child.isCheck = '';
                    });
                    this.payForm.totalPrice = 0;
                    this.checkAllList = [];
                });
            }
        },
        // 选择日期
        onInputBeforeDate(e) {
            this.choosedTime = dayjs(e).format('YYYY年MM月');
            this.dataForm.billMonth = dayjs(e).format('YYYY-MM');
            this.getPayInfo();
            this.show_date = false;
        },
        // 到账
        btnGo() {
            if (this.checkAllList.length == 0) {
                Toast({
                    message: '请选择账单',
                    darution: 900
                });
                return;
            }
            this.showPayed = true;
        },
        // 到账日期-1
        onConfirmTime(event) {
            this.timeShow = false;
            this.payForm.payTime = this.formatDate(event);
        },
        // 到账日期-2
        formatDate(date) {
            date = new Date(date);
            let getFullYear = date.getFullYear();
            let getMonth = date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1;
            let getDate = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
            return `${getFullYear}-${getMonth}-${getDate}`;
        },
        // 收款账户-1
        getType() {
            GetBusinessHotelPayeeAccountByhotel({ hotelId: this.$route.query.hotelId }).done((res) => {
                this.userAll = res.data;
                this.users = res.data.map((item) => item.accountName);
                this.payForm.payeeAccountId = this.userAll[0].id;
                this.payForm.payeeAccountIdValue = this.userAll[0].accountName + '>';
            });
        },
        // 收款账户-2
        onConfirm(val) {
            this.payForm.payeeAccountIdValue = val + '>';
            this.payForm.payeeAccountId = this.userAll.find((item) => item.accountName == val).id;
            this.payeeAccountIdValueShow = false;
        },
        // 选择-支付方式
        onConfirmPayment(val) {
            this.payForm.paymentValue = val + '>';
            this.payForm.payment = this.paymentsAll.find((item) => item.dictLabel == val).dictValue;
            this.paymentShow = false;
        },
        // 确认到账
        confirmPayed() {
            delete this.payForm.payeeAccountIdValue;
            delete this.payForm.username;
            delete this.payForm.paymentValue;
            this.payForm.billSn = this.checkAllList.map((item) => item.billSn).join(',');
            PostBusinessCooperRentalBillAllPayOffline(this.payForm).done((res) => {
                this.showPayed = false;
                this.payForm.totalPrice = null;
                Toast({
                    message: res.msg,
                    duration: 900
                });
                setTimeout('window.history.go(0)', 900);
            });
        }
    }
};
</script>
<style scoped>
.agreementScroll {
    color: #000;
    overflow-y: auto;
}
.noPaid-time {
    display: flex;
    justify-content: space-between;
    padding: 0 40px;
    font-size: 1rem;
    line-height: 50px;
}
.time-title {
    font-weight: bold;
}
.time-price {
    padding-left: 4px;
    font-size: 0.7rem;
}
.my-li {
    background: #fff;
    margin-bottom: 4px;
    color: #b6b3b4;
    font-size: 0.7rem;
}
.w100 {
    width: 50%;
    justify-content: flex-start;
}
.day-content {
    display: flex;
    justify-content: space-between;
    padding: 4px 0;
    align-items: center;
}
.content-top {
    border-bottom: 1px solid #f4f4f4;
}
.content-left {
    text-align: left;
    margin-left: 10px;
    width: 100%;
}
.checkValue {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.bottom-allPrice {
    background: #fff;
    display: flex;
    font-size: 0.8rem;
    align-items: center;
    color: #000;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
}
.bottom-left {
    width: 22%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.bottom-center {
    width: 50%;
    white-space: nowrap;
    text-align: left;
}
.bottom-center label {
    font-weight: bold;
}
.bottom-center label span {
    font-size: 1.2rem;
}
.bottom-right {
    width: 38%;
    background: #ff7d41;
    color: #fff;
}
.payedColor {
    background: #ff7d41;
    color: #fff;
    margin: 10px auto;
    width: 80%;
    border-radius: 5px;
}
/* 已收款 */
.payed_box {
    font-size: 0.8rem;
    margin-top: 10px;
}
.payed_top {
    display: flex;
    padding: 10px 30px;
    align-items: center;
}
.top_l {
    padding-right: 30px;
    display: flex;
    align-items: center;
}
/* 到账 */
/deep/ .van-popup {
    width: 90%;
}
/deep/ .van-field__label {
    text-align: left;
}
/deep/ .van-cell {
    padding: 10px 40px;
    text-align: left;
    align-items: baseline;
}
/deep/ .van-cell__value {
}
/deep/ .van-field__control {
    line-height: 28px;
}
.my-van-icon {
    display: flex;
    align-items: center;
}
.my-color-000 {
    color: #000;
    display: flex;
    align-items: baseline;
}
.db {
    border: 1px solid #98e2c7;
    width: 32%;
    text-align: center;
    color: #0db775;
    border-radius: 4px;
    margin-left: 10px;
}
.ff {
    border: 1px solid #f5cdb4;
    width: 32%;
    text-align: center;
    color: #ff824c;
    border-radius: 4px;
    margin-left: 10px;
}
.m30 {
    margin: 0 30px;
}
.m40 {
    margin: 0 40px;
}
.m20 {
    margin: 0 20px;
}
.p10 {
    padding: 8px 0;
}
.mr10 {
    margin: 0 30px 0 20px;
}
</style>



